import React from 'react'
import img from '../../../Assets/Logo/yaria-creations-high-resolution-logo-transparent (1).png'
import './Header.scss'
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import AccountCircleOutlinedIcon from '@mui/icons-material/AccountCircleOutlined';
const Header = () => {
  return (
    <div className='main-header'>
        <div className='left'>
        <marquee behavior="scroll" direction="right" scrollamount="3" style={{'background':'black','color':'white'}}>
          Taste your style
        </marquee>
        </div>
        <div className='center'>
        <img src={img} alt=''/>
        </div>
        <div className='right'>
            <SearchIcon style={{'cursor':'pointer'}}/>
            <span className='cart-icon'>
            <ShoppingCartOutlinedIcon style={{'cursor':'pointer'}}/>
            <span>0</span>
            </span>
            <FavoriteBorderOutlinedIcon style={{'cursor':'pointer'}}/>
            <span className='account'>
            <li>Login</li>
           <AccountCircleOutlinedIcon/>
            </span>
        </div>
    </div>
  )
}

export default Header